<template>
    <div class="row">
        <div class="col-sm-12 col-md-4" v-for="(property, key) in view">
            <div class="card propCard text-center">
                <img class="card-img-top cardimg" :src="getFirstImage(property)" alt="Card image cap">
                <div class="card-block">
                    <router-link :to="'properties/' + property._id">
                        <h4 class="card-title">{{property.name}}</h4>
                    </router-link>
                    <p class="card-text lot">
                        {{property.currency}} {{property.listPrice}} </p>
                        <p class="col lot-size">Lot Size : {{property.lotSize}}</p>
                    <p class="card-text">{{getShorDesc[key]}}</p>
                    <router-link class="btn viewMore" :to="'properties/' + property._id">
                        View More
                    </router-link>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import myFooter from './myFooter'
export default {
    props: ['view'],
    computed: {
        getShorDesc() {
            let store = []
            this.view.forEach(function(element) {
                var trimmedString = element.description.substr(0, 250);

                //re-trim if we are in the middle of a word
                trimmedString = trimmedString.substr(0, Math.min(trimmedString.length, trimmedString.lastIndexOf(" ")))
                trimmedString = trimmedString + "..."
                store.push(trimmedString)
            }, this);
            return store
        }
    },
    methods:{
        getFirstImage(prop){
            if (!prop.uploadedImages){
                return ""
            }
            return prop.uploadedImages[0] && prop.uploadedImages[0].url || ""
        }
    },
    components: {
        myFooter
    }
}


</script>

<style scoped>
.cardimg {
    height: 14rem;
    padding-top: .50rem;
}

.propCard {
    border: 2px solid brown;
    border-radius: 1rem;
    width: 25rem;
    margin-bottom: 3rem;
}

.lot {
    background-color: #8a1e31;
    color: white;
    font-weight: bold;
}

.viewMore {
    background-color: #8a1e31;
    color: white;
    font-weight: bold;
}
</style>



